#wph-sshare-wizard {
	.box .box-content {
		#wph-sshare--name {
			padding-bottom: 19px;
		}
		#wph-sshare--behav {
			padding-bottom: 9px;
			.tabs .tabs-header {
				margin-bottom: 0;
			}
			.tabs .tabs-header li {
				min-width: 130px;
			}
			.wph-sshare-counter {
				position: relative;
				margin-top: 30px;
				.wph-label--alt {
					display: block;
					padding-left: 50px;
					span, small {
						display: block;
						letter-spacing: -0.05px;
					}
					span {
						color: $color--shaft;
						font: 400 14px/20px $font;
					}
					small {
						margin-top: 5px;
						color: $color--monsoon;
						font: 400 12px/16px $font;
					}
				}
				.toggle {
					position: absolute;
					top: 0;
					left: 0;
				}
			}
		}
		#wph-sshare--social_icons {
			padding-top: 15px;
			padding-bottom: 25px;
			&.linked-icons-enabled {
				padding-top: 20px;
			}
			.wph-label--border {
				margin-bottom: 20px;
			}
			.wph-sshare--pick_social_icons {
				display: flex;
				flex-wrap: wrap;
				&.wph-sshare--native_share {
					flex-direction: row;
					padding-top: 8px;
                    
                    label.toggle-label {
                        display: block;
                    }
				}
				&.wph-sshare--linked_icons {
					flex-direction: column;
					margin-bottom: 20px;
				}
			}
			.wph-sshare--social_native,
			.wph-sshare--social_linked {
				.wph-sshare--icon {
					.wph-social {
						width: 30px;
						height: 30px;
						margin: 0 auto;
						border-radius: 30px;
					}
					&.disabled {
						&, * {
							opacity: 1;
						}
						.wph-social {
							background-color: $color--disabled;
						}
					}
				}
				.wph-icon {
					width: 30px;
					height: 30px;
					display: block;
					margin: 0 auto;
					padding: 7.5px 0 0;
					border-radius: 30px;
					color: $color--white;
					font-size: 15px;
					text-align: center;
					&.i-facebook {
						background: $color--facebook;
					}
					&.i-twitter {
						background: $color--twitter;
					}
					&.i-google {
						padding: 9px 0 0;
						background: $color--google;
						font-size: 12px;
					}
					&.i-pinterest {
						background: $color--pinterest;
					}
					&.i-reddit {
						background: $color--reddit;
					}
					&.i-linkedin {
						background: $color--linkedin;
					}
					&.i-kontakte {
						padding-top: 10px;
						background: $color--kontakte;
						font-size: 10px;
					}
					&.disabled {
						opacity: 1;
						background-color: $color--disabled;
					}
				}
				.wph-input--number {
					width: auto;
					margin-top: 15px;
				}
                .wph-input--number.disabled {
                    display: none;
                }
			}
			.wph-sshare--social_native {
				flex: 0 54px;
				margin-right: 30px;
				.toggle {
					width: 33px;
					display: block;
					margin: 0 auto 20px;
				}
				&:last-child {
					margin-right: 0;
				}
			}
			.wph-sshare--social_linked {
				flex: 1;
				margin-bottom: 30px;
				.wph-sshare--social_wrap {
					display: flex;
					flex-wrap: wrap;
					flex-direction: row;
					align-items: center;
				}
				.wph-sshare--toggle_wrap {
					flex: 0 33px;
					margin-right: 21px;
					.toggle {
						display: block;
						.toggle-label {
							display: block;
						}
					}
				}
				.wph-sshare--icon_wrap {
					flex: 0 30px;
					margin-right: 20px;
					.wph-icon {
						margin-top: 5px;
					}
				}
				.wph-sshare--input_wrap {
					flex: 1;
				}
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}