.button-view-log-list {
	position: relative;
	display: inline-block;
	margin-left: auto;
}
#wpoi-error-list-modal {
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin-top: 32px;
	visibility: hidden;
	opacity: 0;
	transition: 0.4s;

	&.show {
		opacity: 1;
		visibility: visible;
		.wpoi-complete-wrap {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}

	.wpoi-complete-mask {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: $color--modal;
	}
	.wpoi-complete-wrap {
		width: 100%;
		max-width: 588px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 20%);
		transition: opacity 0.3s ease, transform 0.25s linear;
		.box {
			margin: 0;
		}
	}
	.box .box-title {
		height: auto;
		position: relative;
		margin: 0;
		h3 {
			padding: 7px 0 4px;
			line-height: 49px;
			span.wpoi-total-errors {
				display: none;
				position: relative;
				top: -1px;
				margin-left: 15px;
				color: $color--montana;
				font-size: 16px;
				font-family: $font;
				font-weight: 400;
				text-transform: none;
				@include screen(small) {
					display: inline;
				}
			}
		}
		.wph-icon {
			top: 23px;
			right: 30px;
			font-size: 13px;
		}
	}
	.box-content {
		padding: 0;
		border-top: 1px solid $color--lily;
	}
	.wph-table-header {
		th {
			background: $color--alabaster;
			color: $color--shaft;
			font: 400 12px/14px $font;
			letter-spacing: -0.3px;
			text-transform: capitalize;
			text-align: left;
			padding: 5px 0;
			&.column-error {
				width: 60px;
			}
		}
	}
	.wph-table {
		th, td {
			&.column-error {
				width: 60px;
			}
			&:first-child {
				padding-left: 15px;
			}
			&:last-child {
				padding-right: 15px;
			}
		}
	}
	.wpoi-error-list-content {
		height: 284px;
		overflow-x: hidden;
		overflow-y: auto;
	}
	.wpoi-error-list-footer {
		padding: 15px 30px;
		text-align: right;
		border-top: 1px solid $color--lily;
		.wph-button {
			width: 100%;
			display: inline-block;
			margin: 0 auto;
			text-align: center;
			@include screen(small) {
				width: 94px;
				margin: 0 0 0 auto;
			}
		}
	}
	.error-column {
		width: 10%;
	}
	.dashicons {
		cursor: pointer;
	}
	.hustle-delete-logs-confirmation {
		display: inline-block;
		margin-right: 15px;
		display: none;
		label {
			display: inline-block;
			vertical-align: middle;
		}
		.wph-button {
			padding-left: 5px;
			padding-right: 5px;
			width: auto;
		}
	}
}