.wph-social {
	width: auto;
	height: auto;
	display: block;
	box-sizing: border-box;
	fill: $color--white;
	&.wph-facebook {
		background: $color--facebook;
	}
	&.wph-twitter {
		background: $color--twitter;
	}
	&.wph-google {
		background: $color--google;
	}
	&.wph-pinterest {
		background: $color--pinterest;
	}
	&.wph-reddit {
		background: $color--reddit;
	}
	&.wph-linkedin {
		background: $color--linkedin;
	}
	&.wph-vk {
		background: $color--vk;
	}
}

.wph-social-path {
	width: auto;
	height: auto;
	display: block;
	box-sizing: border-box;
	&.wph-facebook {
		fill: $color--facebook;
	}
	&.wph-twitter {
		fill: $color--twitter;
	}
	&.wph-google {
		fill: $color--google;
	}
	&.wph-pinterest {
		fill: $color--pinterest;
	}
	&.wph-reddit {
		fill: $color--reddit;
	}
	&.wph-linkedin {
		fill: $color--linkedin;
	}
	&.wph-vk {
		fill: $color--vk;
	}
}