%left-column {
	&.col-xs-12 {
		padding-left: 0;
	}
	&.col-lg-3 {
		@include screen(large) {
			max-width: 21%;
			flex-basis: 21%;
		}
	}
}

%right-column {
	&.col-xs-12 {
		padding-right: 0;
		padding-left: 17px;
	}
	&.col-lg-9 {
		@include screen(large) {
			max-width: 79%;
			flex-basis: 79%;
		}
	}
}

#wph-sshare-wizard {
	.box .box-title {
		h3 {
			line-height: 59px;
		}
	}
	.box .box-content {
		padding: 0;
		#wph-sshare--name,
		#wph-sshare--behav,
		#wph-sshare--social_icons,
		#wph-sshare--icons_design,
		#wph-sshare--floating_social,
		#wph-sshare--widget_shortcode {
			> div:first-child {
				position: relative;
				@extend %left-column;
			}
			> div:last-child {
				@extend %right-column;
			}
		}
	}
}

#hustle-social-sharing-listings {
	.wpmud-optins-new-button {
		padding: 52px 0;
		text-align: center;
		.wph-button {
			display: inline-block;
		}
	}
	.wph-table {
		tbody tr th.wph-module--name {
			white-space: nowrap;
		}
	}
}

@import "sshare/_tpl-listing.scss";
@import "sshare/_tpl-services.scss";
@import "sshare/_tpl-appearance.scss";

#wph-sshare-wizard {
	.box .box-content {
		#wph-social-sharing--floating-social {
			#wph-social-sharing--floating-social-container {
				padding-bottom: 30px;
			}
		}
	}
}