%module-fields {
	#wph-optin--module-fields {
		border-bottom: 1px solid $color--lilac;
		> div:first-child {
			position: relative;
			@extend %left-column;
		}
		> div:last-child {
			@extend %right-column;
			.wph-label--border {
				margin-bottom: 0;
				border-bottom: 0;
			}
			.wph-table--module_fields {
				padding-right: 30px;
			}
			.wph-new-module {
				padding-top: 15px;
				border-top: 1px solid $color--lilac;
			}
			.add-new-module-field {
				min-width: 108px;
			}
		}
		@include screen(medium) {
			margin-bottom: 25px;
			padding-bottom: 29px;
		}
	}
	#wph-optin--module-fields .wph-table {
		position: relative;
		thead tr th,
		tbody tr td,
		tfoot tr td {
			&.wph-column-label {
				width: 130px;
			}
			&.wph-column-name {
				width: 145px;
				padding-left: 15px;
			}
			&.wph-column-type {
				//width: 136px;
				width: 145px;
				padding-left: 40px;
			}
			&.wph-column-required {
				width: 75px;
				padding-left: 10px;
				text-align: center;
			}
			&.wph-column-placeholder {
				padding-left: 15px;
			}
		}
		thead {
			background: $color--whitesmoke;
			tr th {
				border-top: 1px solid $color--lilac;
				color: $color--montana;
				font: 500 12px/34px $font;
				letter-spacing: 0.1px;
				&.wph-column-label {
					padding-left: 10px;
				}
				&.wph-column-name {
					padding-left: 25px;
				}
				&.wph-column-placeholder {
					padding-left: 25px;
				}
			}
		}
		tbody {
			tr {
				background: $color--white;
			}
			tr:first-child td {
				padding-top: 14px;
			}
			tr td {
				&.wph-column-type {
					letter-spacing: -0.05px;
					text-transform: capitalize;
				}
				&.wph-column-required {
					.wph-input--checkbox {
						margin: 0;
					}
				}
				&.can-trash {
					overflow: initial;
					position: relative;
					span {
						width: 20px;
						height: 20px;
						cursor: pointer;
						opacity: 0;
						position: absolute;
						top: 25px;
						right: -30px;
						border-radius: 30px;
						background: $color--blood;
						text-align: center;
						transition: 0.2s ease;
						.wph-icon {
							position: relative;
							top: -2px;
							color: $color--white;
							font-size: 8px;
						}
						&:hover {
							opacity: 0.6;
						}
					}
				}
			}
			tr:hover td {
				&.can-trash {
					span {
						opacity: 0.6;
					}
				}
			}
		}
		tfoot {
			background: $color--whitesmoke;
			tr:first-child td {
				padding-top: 16px;
				padding-bottom: 0;
			}
			tr td {
				border-top: 0;
				&.wph-column-label {
					padding-left: 15px;
				}
				&.wph-column-type {
					padding-left: 25px;
					.select2-container {
						width: 100% !important;
					}
				}
				&.wph-column-placeholder {
					padding-right: 15px;
				}
			}
			tr:last-child td {
				padding: 15px;
				.wph-button {
					min-width: 72px;
				}
				&:last-child {
					text-align: right;
				}
			}
		}
	}
}