.hustle-two .box {
	display: block;
	margin: 0 0 30px;
	padding: 0;
	border: 0;
	border-radius: 5px;
	background-color: $color--white;
	box-shadow: 0 2px 0 $color--lily;
	.box-title {
		position: relative;
		margin-bottom: -1px;
		padding: 0 20px;
		border-bottom: 1px solid $color--lily;
		h3 {
			overflow: hidden;
			display: block;
			margin: 0;
			padding-right: 30px;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		.wph-icon {
			position: absolute;
			top: 22px;
			right: 30px;
			font-size: 12px;
			color: $color--sand;
			cursor: pointer;
			transition: 0.25s ease;
			&:hover {
				color: $color--montana;
			}
		}
		@include screen(medium) {
			padding: 0 30px;
		}
	}
	.box-content {
		padding: 0 5px;
		@include screen(medium) {
			padding: 0 15px;
		}
	}
	&:last-child {
		margin-bottom: 0;
	}
}

.wpmud-box-simple {
	margin: 0 0 30px;
	border-radius: 5px;
	background: $color--white;
	box-sizing: 0 2px 0 $color--lily;
	.wpmud-body {
		height: 144px;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		align-items: center;
		.wpmud-image {
			height: 144px;
			flex: 0 150px;
			border-radius: 5px 0 0 5px;
			background: url('../img/sprite.png') no-repeat transparent;
			background-image: url('../img/sprite.svg'), none;
			&.wpmud-image-after_content {
				background-position: -1080px 0;
			}
			&.wpmud-image-slide_in {
				background-position: -1290px 0;
			}
			&.wpmud-image-widget {
				background-position: -1500px 0;
			}
			&.wpmud-image-shortcode {
				background-position: -1710px 0;
			}
			&.wpmud-image-pop_up {
				background-position: -1080px -210px;
			}
		}
		.wpmud-content {
			flex: 1;
			padding: 0 30px;
			h1, h2, h3, h4, h5, h6 {
				font-size: 18px;
				line-height: 20px;
				letter-spacing: -0.45px;
			}
			p {
				margin: 5px 0 0;
				font-size: 14px;
				line-height: 20px;
			}
		}
	}
	&:only-child, &:last-child {
		margin: 0;
	}
}