#wpoi-box-dashboard-sshare-stats {
	.wph-table {
		margin: 30px 0;
		thead tr th.wph-module--count,
		tbody tr th.wph-module--count {
			width: 130px;
			text-align: right;
		}
		tfoot tr td {
			text-align: center;
			.wph-button {
				display: inline-block;
			}
		}
	}
	.wph-sshare_no_stats {
		padding: 30px 0;
		.wph-label--notice {
			margin: 0;
		}
	}
}

// Styles for modals
#wpoi-sshare-stats-modal {
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	margin-top: 32px;
	visibility: hidden;
	opacity: 0;
	transition: 0.4s;

	&.show {
		opacity: 1;
		visibility: visible;
		.wpoi-complete-wrap {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}

	.wpoi-complete-mask {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: $color--modal;
	}
	.wpoi-complete-wrap {
		width: 100%;
		max-width: 588px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, 20%);
		transition: opacity 0.3s ease, transform 0.25s linear;
		.box {
			margin: 0;
		}
	}
	.box .box-title {
		height: auto;
		position: relative;
		margin: 0;
		h3 {
			padding: 7px 0 4px;
			line-height: 49px;
			span.wpoi-total-errors {
				display: none;
				position: relative;
				top: -1px;
				margin-left: 15px;
				color: $color--montana;
				font-size: 16px;
				font-family: $font;
				font-weight: 400;
				text-transform: none;
				@include screen(small) {
					display: inline;
				}
			}
		}
		.wph-icon {
			top: 23px;
			right: 30px;
			font-size: 13px;
		}
	}
	.box-content {
		padding: 0 15px;
		.wph-table {
			margin: 30px 0;
			thead tr th,
			tbody tr td,
			tbody tr th {
				&:first-child {
					padding-right: 10px;
					padding-left: 0;
				}
				&.wph-module--count {
					width: 140px;
					text-align: right;
				}
				&:last-child {
					padding-right: 0;
				}
			}
		}
	}
}