#wph-sshare-wizard {
	.box .box-content {
		#wph-sshare--icons_design {
			margin-bottom: 25px;
			padding-bottom: 30px;
			border-bottom: 1px solid $color--lilac;
			.wph-label--border {
				
				+ label {
					margin-bottom: 10px;
				}
			}
			.tabs .tabs-header {
				margin: 0 0 30px;
			}
			.tabs .tabs-header li {
				min-width: 59px;
				label {
					padding: 5px;
					.wph-social,
					.wph-social-path {
						width: 18px;
						height: 18px;
						margin: 0 auto;
					}
				}
				label[for="wph-sshare-icons_design_one"] {
					.wph-social-path .wph-social-icon {
						fill: $color--manatee;
					}
				}
				label[for="wph-sshare-icons_design_two"] {
					.wph-social {
						border: 1px solid $color--manatee;
						border-radius: 20px;
						background: transparent;
						.wph-social-icon {
							fill: $color--manatee;
						}
					}
				}
				label[for="wph-sshare-icons_design_two"],
				label[for="wph-sshare-icons_design_three"] {
					.wph-social {
						border-radius: 20px;
					}
				}
				label[for="wph-sshare-icons_design_three"],
				label[for="wph-sshare-icons_design_four"] {
					.wph-social {
						background: $color--manatee;
						.wph-social-icon {
							fill: $color--white;
						}
					}
				}
				&:hover, &.current {
					label[for="wph-sshare-icons_design_one"] {
						.wph-social-path .wph-social-icon {
							fill: $color--white;
						}
					}
					label[for="wph-sshare-icons_design_two"] {
						.wph-social {
							border: 1px solid $color--white;
							.wph-social-icon {
								fill: $color--white;
							}
						}
					}
					label[for="wph-sshare-icons_design_three"],
					label[for="wph-sshare-icons_design_four"] {
						.wph-social {
							background: $color--white;
							.wph-social-icon {
								fill: $color--manatee;
							}
						}
					}
				}
			}
			#wph-sshare-icons_reorder {
				label {
					cursor: text;
					display: inline-block;
					margin: 0;
					letter-spacing: 0.35px;
					+ small {
						display: block;
					}
				}
				.wph-sshare-reorder_box {
					display: flex;
					align-items: center;
					margin: 17px 0 0;
					padding: 28px 29px;
					border: 2px dashed rgba(217, 217, 217, 0.9);
					border-radius: 3px;
					.wph-sshare-reorder_item {
						margin-right: 20px;
						.wph-social,
						.wph-social-path {
							max-width: 30px;
							max-height: 30px;
							margin: 0 auto;
						}
						&:last-child {
							margin-right: 0;
						}
					}
					&.wph-sshare-icons_design_one {
						.wph-social-path {
							.wph-social-icon {
								fill: $color--manatee;
							}
						}
					}
					&.wph-sshare-icons_design_two,
					&.wph-sshare-icons_design_three {
						.wph-social {
							border-radius: 40px;
						}
					}
					&.wph-sshare-icons_design_two {
						.wph-social {
							border: 1px solid $color--manatee;
							background: transparent;
							path {
								fill: $color--manatee;
							}
						}
					}
					&.wph-sshare-icons_design_three,
					&.wph-sshare-icons_design_four {
						.wph-social {
							background: $color--manatee;
							.wph-social-icon {
								fill: $color--white;
							}
						}
					}
				}
			}
		}
		#wph-sshare--floating_social {
			margin-bottom: 25px;
			padding-bottom: 30px;
			border-bottom: 1px solid $color--lilac;
			.row {
				.col-lg-10 {
					@include screen(large) {
						max-width: 81%;
						flex-basis: 81%;
						padding-left: 0;
					}
				}
				.col-lg-2 {
					@include screen(large) {
						max-width: 19%;
						flex-basis: 19%;
						padding-right: 0;
					}
				}
			}
			.tabs {
				position: relative;
				z-index: 3;
			}
			.tabs .tabs-header {
				margin-bottom: 14px;
			}
			.tabs .tabs-header li {
				min-width: 130px;
			}
			.tabs .tabs-body {
				#wps-color--custom {
					padding: 30px 15px;
					border-radius: 5px;
					background: $color--storm;
					.row {
						position: relative;
						&:nth-child(1) {
							z-index: 2;
						}
						&:nth-child(2) {
							z-index: 1;
						}
					}
					.wph-pickers--single {
						display: inline-flex;
					}
					label {
						display: inline-block;
						position: relative;
						top: -2px;
						margin: 0 0 0 6px;
					}
				}
			}
			#wph-sshare--container_bg {
				position: relative;
				z-index: 2;
				margin: 30px 0 28px;
				.row .col:not(:first-child) {
					margin-left: 20px;
				}
				.wph-pickers--single {
					display: inline-flex;
				}
				label {
					display: inline-block;
					position: relative;
					top: -2px;
					margin: 0 0 0 6px;
				}
			}
			#wph-sshare--shadow {
				position: relative;
				z-index: 1;
				.wph-label--checkbox {
					margin: 20px 0 12px;
				}
				#wph-sshare--shadow_settings {
					margin-bottom: 30px;
					padding: 20px 15px 30px;
					border-radius: 5px;
					background: $color--storm;
					.wph-label--alt {
						margin-bottom: 5px;
					}
					.wph-input--number {
						width: 70px;
					}
				}
			}
			.wph-sshare--preview_box {
				margin: 0;
				.wph-sshare--container a {
					pointer-events: none;
				}
			}
		}
		#wph-sshare--widget_shortcode {
			padding-bottom: 25px;
			.tabs .tabs-header {
				margin-bottom: 14px;
			}
			.tabs .tabs-header li {
				min-width: 130px;
			}
			.tabs .tabs-body {
				#wps-sshare--widget_custom_colors {
					padding: 30px 15px;
					border-radius: 5px;
					background: $color--storm;
					.row {
						position: relative;
						z-index: 2;
					}
					.wph-pickers--single {
						display: inline-flex;
					}
					label {
						display: inline-block;
						position: relative;
						top: -2px;
						margin: 0 0 0 6px;
					}
				}
			}
			#wph-sshare--widget_container_bg {
				position: relative;
				z-index: 1;
				margin: 30px 0 28px;
				.wph-pickers--single {
					display: inline-flex;
				}
				label {
					display: inline-block;
					position: relative;
					top: -2px;
					margin: 0 0 0 6px;
				}
				.row .col {
					&:not(:first-child) {
						margin-left: 20px;
					}
				}
			}
			#wph-sshare--widget_shadow {
				.wph-label--checkbox {
					margin: 20px 0 12px;
				}
				#wph-sshare--widget_shadow_settings {
					margin-bottom: 30px;
					padding: 20px 15px 30px;
					border-radius: 5px;
					background: $color--storm;
					.wph-label--alt {
						margin-bottom: 5px;
					}
					.wph-input--number {
						width: 70px;
					}
				}
			}
			.wph-sshare--preview_group {
				margin-bottom: 33px;
			}
			.wph-sshare--preview_box {
				margin: 0;
				.wph-sshare--container a {
					pointer-events: none;
				}
			}
		}
	}
}